import CurrentPost from '../Pages/CurrentPost';
import InMyRoom from '../Pages/InMyRoom';
import PostsPage from '../Pages/PostsPage';
import TablePage from '../Pages/TablePage';

export const Routes = [
	{ path: 'react-projects/posts', component: PostsPage, exact: true },
	{ path: 'react-projects/posts/:id', component: CurrentPost, exact: true },
	{ path: 'react-projects/products', component: InMyRoom, exact: true },
	{ path: 'react-projects/table', component: TablePage, exact: true },
];
